import { Component, ElementRef, forwardRef, Input, OnInit, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
    selector: 'lf-currency',
    template: `<div>
                <mat-form-field appearance="outline" class="lf-currency">
                    <mat-label>{{label}}</mat-label>
                    <div *ngIf="value" class="lf-input-prefix">{{currency=='USD'?'$ ':'â‚¹ '}}</div>
                    <input matInput placeholder="" [(ngModel)]="value" (focusout)="onBlur()" #input 
                    (change)="onChange()" [disabled]="disabled" style="display:inline;">
                </mat-form-field>
              </div>`,
    styles: [`
            ::ng-deep .lf-currency .mat-form-field-infix{
                    display: flex;
                }
    
                ::ng-deep .lf-input-prefix{
                    margin-right: 3px;
                    margin-top: -0.0625em;
                    color: #555;
                }
            `],
    providers: [{
        provide: NG_VALUE_ACCESSOR,
        useExisting: forwardRef(() => LfCurrencyComponent),
        multi: true
    }]
})
export class LfCurrencyComponent implements ControlValueAccessor, OnInit {

    @Input() label: string = '';
    @Input() disabled: boolean = false;
    @ViewChild('input') input: ElementRef = { nativeElement: '' };

    private innerValue: any = '';

    @Input() currency: string = 'INR';

    private onTouchedCallback: () => void = () => { };
    private onChangeCallback: (_: any) => void = (_: any) => { };

    get value(): any {
        return this.innerValue;
    };

    set value(v: any) {
        let val = this.value
        if(!isNaN(this.parse(v))){
          this.innerValue = v;
          this.input.nativeElement.value = this.transform(this.parse(v))
          this.onChangeCallback(v);
        } else{
          this.value = val
        }
    }

    ngOnInit() {
        setTimeout(() => {
            if(this.value){
                this.input.nativeElement.value = this.transform(this.value)
            }
        }, 0);
    }

    onBlur() {
        let elem = this.input.nativeElement;
        (this.value)?elem.value = this.transform(this.value):false;
        this.onTouchedCallback();
    }

    writeValue(value: any) {
        this.innerValue = value;
    }

    registerOnChange(fn: any) {
        this.onChangeCallback = fn;
    }

    registerOnTouched(fn: any) {
        this.onTouchedCallback = fn;
    }

    transform(number: any) {
        if (this.currency === 'US') {
            let string = number + ''
            return string.replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")
        }
        else {
            number = Math.round(number * 100) / 100;
            let numberStr = number.toString();
            let breaker = numberStr.split(".");
            numberStr = breaker[0];
            let decimals = breaker[1] ? `.${breaker[1]}` : "";
            let thousandsMatcher = /(\d+)(\d{3})$/;
            let thousandsAndRest = thousandsMatcher.exec(numberStr);
            if (!thousandsAndRest) return numberStr + decimals;
            let val = thousandsAndRest[1].replace(/\B(?=(\d{2})+(?!\d))/g, ",") + "," + thousandsAndRest[2];
            return val + decimals;
        }
    }

    parse(value: any) {
        value = String(value)
        return value.split(",").join("");
    }

    onChange() {
        this.value = Number(this.parse(this.value))
    }
}
