import { Component, ElementRef, forwardRef, Input, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
    selector: 'lf-mat-input',
    template: `<div>
                <mat-form-field appearance="outline" class="lf-mat-input">
                    <mat-label>{{label}}</mat-label>
                    <div *ngIf="value && prefix" class="lf-input-prefix">{{prefix}}</div>
                    <input matInput placeholder="" [(ngModel)]="value" #input [disabled]="disabled" (focusout)="onBlur()" 
                    [imask]="{mask:mask}" [unmask]="true" [errorStateMatcher]='matcher' [required]="required">
                    <mat-error class="MT10">{{errMessage}}</mat-error>
                </mat-form-field>
              </div>`,
    styles: [`
                ::ng-deep .lf-mat-input .mat-form-field-infix{
                    display: flex;
                }
                
                .lf-mat-input .lf-input-prefix{
                    margin-right: 4px;
                    margin-top: -0.0625em;
                    color: #777;
                }
    `],
    providers: [{
        provide: NG_VALUE_ACCESSOR,
        useExisting: forwardRef(() => LfInputComponent),
        multi: true
    }]
})
export class LfInputComponent implements ControlValueAccessor {

    @Input() label: string = '';
    @Input() prefix: string;
    @Input() errMessage: string = '';
    @Input() errorMessage: string = '';
    @Input() disabled: boolean = false;
    @Input() required: boolean = false;
    @Input() validate: (v: any) => boolean = (v: any) => true;
    @Input() upCase: boolean = false;
    @Input() region: string = '';
    @Input() mask: any = /\^*$/;

    @ViewChild('input') input: ElementRef = { nativeElement: '' };

    private innerValue: any = '';
    private validator: boolean = false;
    public matcher = { isErrorState: () => this.validator }

    private onTouchedCallback: () => void = () => { };
    private onChangeCallback: (_: any) => void = (_: any) => { };

    get value(): any {
        return this.innerValue;
    };

    set value(v: any) {
        this.innerValue = v;
        this.onChangeCallback(v);
    }

    writeValue(value: any) {
        this.innerValue = value;
    }

    registerOnChange(fn: any) {
        this.onChangeCallback = fn;
    }

    registerOnTouched(fn: any) {
        this.onTouchedCallback = fn;
    }

    onBlur() {
        (this.upCase && this.value) ? this.value = this.value.toUpperCase() : true;
        let requiredFn = () => {
            this.validator = this.required;
            this.errMessage = 'This field is required.';
        }
        let validateFn = () => {
            this.validator = !this.validate(this.value);
            this.errMessage = this.errorMessage;
        }
        (this.value) ? validateFn() : requiredFn();
        this.onTouchedCallback();
    }
}
